.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  min-height: 100vh;
}

.App-header {
  background-color: #222;
  height: 70px;
  line-height: 70px;
  color: white;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.App-header h1 {
  margin: 0;
  width: 50vw;
  text-align: left;
  padding-left: 40px;
}

.App-header h1 a {
  text-decoration: none;
  color: white;
}

.App-header h1 a:hover {
  color: #ddd;
}



.App-header .link-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding-right: 10px;
}

.App-header .link-container a {
  text-decoration: none;
}

.App-header .link-container div {
  color: white;
  line-height: 70px;
  padding: 0 8px;
}

.App-header .link-container div:hover {
  text-shadow: 0px 1px white, 1px 0px white;
}

.App-footer {
  background-color: #eee;
  height: 40px;
  line-height: 40px;
  color: #000;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.App-title {
  font-size: 1.5em;
}


@media( max-width: 550px ){
  .App-header h1 {
    width: 100%;
  }

  .App-header .link-container {
    display: none;
  }
}



